.blog {

	.site {
		display: grid;
		grid-template-columns: auto ($size__site-sidebar);
		grid-template-areas:
			"header"
			"main"
			"sidebar"
			"footer";

		@include mq(mobile-sm) {
			display: grid;
			grid-template-columns: auto;
			grid-template-areas:
				"header header"
				"main sidebar"
				"footer footer";
		}
	}

	.site-header {
		grid-area: header;
	}

	.site-main {
		grid-area: main;
		overflow: hidden;
		/* Resolves issue with <pre> elements forcing full width. */
	}

	.widget-area {
		grid-area: sidebar;
	}

	.site-footer {
		grid-area: footer;
	}
}